import React, { Component, Fragment } from "react";
import styled from "styled-components";

const WomanWrapper = styled.div`
  width: 100vw;
  outline: 1px solid ${props => props.theme.green};
  display: flex;
  justify-content: center;
  align-items: center;

  h1 {
    color: ${props => props.theme.green};
    text-transform: uppercase;
  }
`;

const ContainerTop = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid red;
`;

const ContainerMiddle = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid blue;
  display: none;
`;

const ContainerBottom = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid yellow;
  display: none;
`;

const calcX = x => {
  if (typeof window === "undefined") {
    return 0;
  } else {
    // measure centerpoint
    const winWidth = window.innerWidth;
    const center = winWidth / 2;
    const distFromCenter = x - center;
    const decimal = distFromCenter / center;
  }
};

export const WomenList = ({ women, x, y }) => {
  if (women) {
    const allWomen = women.map(woman => {
      return (
        <WomanWrapper skewX={calcX(x)} key={woman.id}>
          <h1>{woman.name}</h1>
        </WomanWrapper>
      );
    });

    const containers = (
      <Fragment>
        <ContainerTop>{allWomen}</ContainerTop>
        <ContainerMiddle>{allWomen}</ContainerMiddle>
        <ContainerBottom>{allWomen}</ContainerBottom>
      </Fragment>
    );

    return containers;
  } else return null;
};
